'use strict';

(function() {
    let snd = new Audio("../../assets/sounds/boom.mp3"); // –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –±—É—Ñ–µ—Ä–∏–∑—É–µ—Ç—Å—è
	let boom;
	let	boomImage;
	let	canvas;
	let requestId;

	// –ü–æ–ª—É—á–∏—Ç—å —Ö–æ–ª—Å—Ç
	canvas = document.querySelector('.boomAnimationActive');
	canvas.width = 120;
	canvas.height = 120;

	// –°–æ–∑–¥–∞—Ç—å –ª–∏—Å—Ç –¥–ª—è —Å–ø—Ä–∞–π—Ç–æ–≤
	boomImage = new Image();

	// –°–æ–∑–¥–∞–Ω–∏–µ —Å–ø—Ä–∞–π—Ç–∞
	boom = sprite({
		context: canvas.getContext("2d"),
		width: 1024,
		height: 64,
		image: boomImage,
		numberOfFrames: 16,
		ticksPerFrame: 4
	});

	// –ó–∞–≥—Ä—É–∑–∏—Ç—å –ª–∏—Å—Ç —Å–ø—Ä–∞–π—Ç–∞
	boomImage.src = "../../assets/images/img/boom-sprite-animation.png";

	function gameLoop () {
		requestId = undefined;
		startBoomAnimation();
	  boom.update();
		boom.render();
	}

	function sprite (options) {
		let that = {};
		let frameIndex = 0;
		let	tickCount = 0;
		let	ticksPerFrame = options.ticksPerFrame || 0;
		let	numberOfFrames = options.numberOfFrames || 1;
		that.context = options.context;
		that.width = options.width;
		that.height = options.height;
		that.image = options.image;
		that.update = function () {
      tickCount += 1;
      if (tickCount > ticksPerFrame) {
				tickCount = 0;
	      // –ï—Å–ª–∏ —Ç–µ–∫—É—â–∏–π –∏–Ω–¥–µ–∫—Å –∫–∞–¥—Ä–∞ –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –≤ –¥–∏–∞–ø–∞–∑–æ–Ω–µ
        if (frameIndex < numberOfFrames - 1) {
        // –ü–µ—Ä–µ–π–¥–∏—Ç–µ –∫ —Å–ª–µ–¥—É—é—â–µ–º—É –∫–∞–¥—Ä—É
          frameIndex += 1;
        } else {
          frameIndex = 0;
				}
				if (frameIndex == 15){
					stopBoomAnimation();  // –û—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∞–Ω–∏–º–∞—Ü–∏—é –ø–æ—Å–ª–µ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏ —Å–ø—Ä–∞–π—Ç–∞
				}
      }
    };
		that.render = function () {
			// –û—á–∏—Å—Ç–∏—Ç—å —Ö–æ–ª—Å—Ç
			that.context.clearRect(0, 0, that.width, that.height);
			// –†–∏—Å—É–µ–º –∞–Ω–∏–º–∞—Ü–∏—é
			that.context.drawImage(
				that.image,
				frameIndex * that.width / numberOfFrames,
				0,
				that.width / numberOfFrames,
				that.height,
				0,
				0,
				that.width / numberOfFrames,
				that.height);
		};
		return that;
	}

	function startBoomAnimation() {
		if (!requestId) {
			requestId = window.requestAnimationFrame(gameLoop);
		}
	}

	function stopBoomAnimation() {
		if (requestId) {
			window.cancelAnimationFrame(requestId);
			requestId = undefined;
		}
	}

  function boomAnimationHero() { // –¥–ª—è 500px
    canvas.style.left = "500px"; // "400px"
    canvas.style.top =  "110px"; // "-30px"
    startBoomAnimation();
    snd.play(); // –∑–≤—É–∫ –≤–∑—Ä—ã–≤–∞
  }

  function boomAnimationEnemy() { // –¥–ª—è 500px
    canvas.style.left = "1300px"; //"930px"
    canvas.style.top = "110px"; //"-30px"
    startBoomAnimation();
    snd.play(); // –∑–≤—É–∫ –≤–∑—Ä—ã–≤–∞
  }

  // –ø–µ—Ä–µ–Ω–æ—Å –≤ –≥–ª–æ–±–∞–ª—å–Ω—É—é –æ–±–ª–∞—Å—Ç—å –≤–∏–¥–∏–º–æ—Å—Ç–∏
  window.boomAnimation = {
    boomAnimationEnemy: boomAnimationEnemy,
    boomAnimationHero: boomAnimationHero
  };

}());
