'use strict';

(function () {
  const fieldsRadioInput = document.querySelectorAll('.fields-radio__input');
  const characterName = document.querySelector('.game__nickname--character');
  let indexSelectedCharacter; // –∏–Ω–¥–µ–∫—Å –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ –≥–ª–∞–≤–Ω–æ–≥–æ –ø–µ—Ä—Å–æ–Ω–∞–∂–∞

  for (let i = 0; i < fieldsRadioInput.length; i++) {
    fieldsRadioInput[i].addEventListener('click', function() {
      window.animation.character.innerHTML = ''; // —É–±—Ä–∞—Ç—å –ø—Ä–µ–¥—ã–¥—É—â –¥–æ–±–∞–≤–ª–µ–Ω–Ω—ã–π —ç–ª-—Ç
      characterName.innerHTML = ''; // —É–±—Ä–∞—Ç—å –∏–º—è –ø—Ä–µ–¥—ã–¥—É—â–µ–≥–æ
      characterName.innerHTML = fieldsRadioInput[i].getAttribute('data-name'); // –¥–æ–±–∞–≤–∏—Ç—å –∏–º—è
      indexSelectedCharacter = fieldsRadioInput[i].value;
      window.animation.character.appendChild(window.base–°onfig.heroesArray[indexSelectedCharacter]);
    });
  }

  // –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –¥–æ–±–∞–≤–ª—è–µ–º –ø–µ—Ä–≤—ã–π —ç–ª–µ–º–µ–Ω—Ç
  if (!indexSelectedCharacter) {
    indexSelectedCharacter = 0;
    window.animation.character.appendChild(window.base–°onfig.heroesArray[indexSelectedCharacter]);
    characterName.innerHTML = fieldsRadioInput[indexSelectedCharacter].getAttribute('data-name'); // –¥–æ–±–∞–≤–∏—Ç—å –∏–º—è –ö–∞—Ä—Ç–º–∞–Ω–∞ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
  }

})();
