'use strict';

(function () {
  const ENTER_KEYCODE = 13;
  const ESC_KEYCODE = 27;
  const fieldsTextContainer = document.querySelectorAll('.fields-text__items');
  const fieldsCheckboxContainer = document.querySelectorAll('.fields-checkbox__items');
  const fieldsRadioContainer = document.querySelectorAll('.fields-radio__items');
  const btnStartGame = document.querySelector('.field-action__btn');

  const pageHeaderGame = document.querySelector('.page-header-game');
  const slogan = document.querySelector('.slogan');
  const form = document.querySelector('.form');
  const pageFooter = document.querySelector('.page-footer');

  const game = document.querySelector('.game');
  const gameLevelMessage = document.querySelector('.game__level-message');
  const btnsInGame = document.querySelectorAll('.game__btn');
  const btnExitInGame = document.querySelector('.game__btn--exit');
  const btnNextRoundInGame = document.querySelector('.game__btn--next-round');

  const btnPopupFailure = document.querySelector('.popup__btn--failure');

  const popupIncantations = document.querySelector('.popup--incantations');
  const popupIncantationLinks = document.querySelector('.popup__incantation-links');
  const incantationBtn = document.querySelectorAll('.popup__incantation-btn');

  const popupScore = document.querySelector('.popup--score');
  const btnPopupContinueGame = document.querySelector('.popup__btn--game');
  const btnPopupExitFromGame = document.querySelector('.popup__btn--exit');

  // —Ñ—É–Ω–∫—Ü–∏—è –¥–µ–ª–∞–µ—Ç –Ω–µ–¥–æ—Å—Ç—É–ø–Ω—ã–º–∏ –≤—Å–µ –ø–æ–ª—è —Ñ–æ—Ä–º
  let makeDisabledBobyAndForms = function () {
    fieldsTextContainer.forEach(function(el) {
      el.setAttribute('disabled', '');
    });
    fieldsCheckboxContainer.forEach(function(el) {
      el.setAttribute('disabled', '');
    });
    fieldsRadioContainer.forEach(function(el) {
      el.setAttribute('disabled', '');
    });
    btnStartGame.setAttribute('disabled', '');
    pageHeaderGame.classList.add('page-header-game--disabled');
    slogan.classList.add('slogan--disabled');
    form.classList.add('form--disabled');
    pageFooter.classList.add('page-footer--disabled');
    document.body.classList.add('body--disabled');
  }

  // —Ñ—É–Ω–∫—Ü–∏—è –∞–∫—Ç–∏–≤–∞—Ü–∏–∏ –ø–æ–ª–µ–π —Ñ–æ—Ä–º
  let activateBobyAndForms = function () {
    fieldsTextContainer.forEach(function(el) {
      el.removeAttribute('disabled');
    });
    fieldsCheckboxContainer.forEach(function(el) {
      el.removeAttribute('disabled');
    });
    fieldsRadioContainer.forEach(function(el) {
      el.removeAttribute('disabled');
    });
    btnStartGame.removeAttribute('disabled');
    pageHeaderGame.classList.remove('page-header-game--disabled');
    slogan.classList.remove('slogan--disabled');
    form.classList.remove('form--disabled');
    pageFooter.classList.remove('page-footer--disabled');
    document.body.classList.remove('body--disabled');
  }

  // –≤–∞–ª–∏–¥–∞—Ü–∏—è –Ω–∞ –≤—Ö–æ–¥ –≤ –∏–≥—Ä—É
  btnStartGame.addEventListener('click', function(event) {
    event.preventDefault();
    if (document.getElementsByName('name')[0].value
    && document.getElementsByName('surname')[0].value
    && document.getElementsByName('email')[0].value) {
      init(); // –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –∏–≥—Ä—ã
    } else {
      popupFailure.classList.add('popup--show');
      makeDisabledBobyAndForms();
    }
  });

  // –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∑–∞–∫—Ä—ã—Ç–∏—è –ø–æ–ø–∞–ø–∞
  btnPopupFailure.addEventListener('click', function() {
    if (popupFailure.classList.contains('popup--show')) {
      popupFailure.classList.remove('popup--show');
      activateBobyAndForms(); // –∞–∫—Ç–∏–≤–∞—Ü–∏—è —Å—Ç—Ä–∞–Ω–∏—Ü—ã
    }
  });

  // –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∑–∞–∫—Ä—ã—Ç–∏—è –ø–æ–ø–∞–ø–æ–≤ ESC
  window.addEventListener('keydown', function(event) {
    if (event.keyCode === ESC_KEYCODE) {
      if (popupFailure.classList.contains('popup--show')) {
        popupFailure.classList.remove('popup--show');
      }
    }
  });

  /* ----- –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –∑–∞–∫–ª–∏–Ω–∞–Ω–∏–π ----- */

  // —Ñ—É–Ω–∫—Ü–∏—è –¥–µ–ª–∞–µ—Ç –Ω–µ–¥–æ—Å—Ç—É–ø–Ω—ã–º–∏ –∫–Ω–æ–ø–∫–∏ –≤ –∏–≥—Ä–µ
  let makeDisabledBtnsInGame = function () {
    btnsInGame.forEach(function(el) {
      el.setAttribute('disabled', '');
    });
  }

  // —Ñ—É–Ω–∫—Ü–∏—è –¥–µ–ª–∞–µ—Ç –Ω–µ–¥–æ—Å—Ç—É–ø–Ω—ã–º –±–æ–¥–∏ –≤ –∏–≥—Ä–µ
  let makeDisabledBobyInGame = function () {
    game.classList.add('game--disabled');
    document.body.classList.add('body--disabled');
  }

  // —Ñ—É–Ω–∫—Ü–∏—è –∞–∫—Ç–∏–≤–∞—Ü–∏–∏ –∫–Ω–æ–ø–æ–∫ –∏ –±–æ–¥–∏ –≤ –∏–≥—Ä–µ
  let activateBtnsInGame = function () {
    btnsInGame.forEach(function(el) {
      el.removeAttribute('disabled');
    });
  }

  // —Ñ—É–Ω–∫—Ü–∏—è –∞–∫—Ç–∏–≤–∞—Ü–∏–∏ –∫–Ω–æ–ø–æ–∫ –∏ –±–æ–¥–∏ –≤ –∏–≥—Ä–µ
  let activatedBobyInGame = function () {
    game.classList.remove('game--disabled');
    document.body.classList.remove('body--disabled');
  }

  // —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ –∑–∞–∫–ª–∏–Ω–∞–Ω–∏—è
  function checkSelectedIncantation(obj) {
    if (obj.getAttribute('data-value') == 'restore') {
      window.popups.incantationIndicator = 'restore'; // –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –≤—ã–±—Ä–∞–Ω–Ω–æ–µ –∑–∞–∫–ª–∏–Ω–∞–Ω–∏–µ
    }
    if (obj.getAttribute('data-value') == 'power1x') {
      window.popups.incantationIndicator = 'power1x';
    }
    if (obj.getAttribute('data-value') == 'power2x') {
      window.popups.incantationIndicator = 'power2x';
    }
  }

  // –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –∑–∞–∫–ª–∏–Ω–∞–Ω–∏–π
  function showPopupIncantations() {
    setTimeout(function() {
      popupIncantations.classList.remove('popup--hidden');
      popupIncantations.classList.add('popup--show');
      makeDisabledBobyInGame(); // —Å–∫—Ä—ã—Ç–∏–µ –±–æ–¥–∏ –≤–æ –≤—Ä–µ–º—è –ø–æ–ø–∞–ø–∞ –∑–∞–∫–ª–∏–Ω–∞–Ω–∏–π
    }, 6500);
  }

  // –∑–∞–∫—Ä—ã—Ç–∏–µ –∏ –∞–Ω–∏–º–∞—Ü–∏—è –∫–Ω–æ–ø–æ–∫ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –∑–∞–∫–ª–∏–Ω–∞–Ω–∏–π
  let btnSelectIncantationHandler = function(e) {
    e.preventDefault;
    if (e.target.classList.contains('popup__incantation-btn')) {
      e.target.classList.remove('animate');
      e.target.classList.add('animate');
      setTimeout(function(){
        e.target.classList.remove('animate');
      }, 700);
      setTimeout(function(){
        popupIncantations.classList.remove('popup--show');
        popupIncantations.classList.add('popup--hidden');
      }, 400);
    }
    checkSelectedIncantation(e.target); // —É–∑–Ω–∞—Ç—å –≤—ã–±—Ä–∞–Ω–Ω–æ–µ –∑–∞–∫–ª–∏–Ω–∞–Ω–Ω–∏–µ
  };

  for (let i = 0; i < incantationBtn.length; i++) {
    incantationBtn[i].addEventListener('click', btnSelectIncantationHandler);
  }

  /* ----- –æ–∫–Ω–æ –∏–Ω—Ñ—ã –æ —Ä–∞—É–Ω–¥–µ ----- */

  // –æ–±–æ–±—Ä–∞–∑–∏—Ç—å –∏–Ω—Ñ—É –æ —Ä–∞—É–Ω–¥–µ
  function showLevelMessage() {
    setTimeout(function() {
      gameLevelMessage.classList.add('game__level-message--show');
    }, 3000);
    setTimeout(function() {
      gameLevelMessage.classList.remove('game__level-message--show');
    }, 6000);
  }

  /* ----- –∫–Ω–æ–ø–∫–∞ –≤—ã—Ö–æ–¥–∞ –∏–∑ –∏–≥—Ä–æ–≤–æ–≥–æ –ø–æ–ª—è –¥–æ—Å—Ä–æ—á–Ω–æ----- */

  // —Ñ—É–Ω–∫—Ü–∏—è –≤—ã—Ö–æ–¥–∞ –∏–∑ –∏–≥—Ä–æ–≤–æ–≥–æ –ø–æ–ª—è
  function hidePlayingField() {
    pageHeaderGame.classList.remove('page-header-game--hidden');
    slogan.classList.remove('slogan--hidden');
    form.classList.remove('form--hidden');
    pageFooter.classList.remove('page-footer--hidden');
    game.classList.remove('game--show');
  }

  // —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è –≤—ã—Ö–æ–¥–∞ –∏–∑ –∏–≥—Ä—ã –∏ –æ–±–Ω—É–ª–µ–Ω–∏—è –∂–∏–∑–Ω–µ–π, –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤—Ä–∞–≥–æ–≤
  let btnExitClickHandler = function () {
    window.mechanicsGame.resetResultsOfLife(); // –æ–±–Ω—É–ª–∏—Ç—å –∂–∏–∑–Ω—å –≤—Å–µ–º –ø–µ—Ä—Å–∞–º
    window.enemyCreate.resetEnemyComponents(); // —Å–∫–∏–Ω—É—Ç—å –æ—Ç—Ä–µ–Ω–¥–æ—Ä–µ–Ω–Ω—ã–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –≤—Ä–∞–≥–∞
    hidePlayingField(); // –≤—ã—Ö–æ–¥ –∏–∑ –∏–≥—Ä–æ–≤–æ–≥–æ –ø–æ–ª—è
  }

  btnExitInGame.addEventListener('click', btnExitClickHandler);

  /* ----- –ø–æ–ø–∞–ø —Å —Ç–∞–±–ª–∏—Ü–µ–π –±–∞–ª–ª–æ–≤ ----- */

  // –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –ø–æ–ø–∞–ø —Å–æ —Å—á—ë—Ç–æ–º
  function showPopupScore() {
    popupScore.classList.remove('popup--hidden');
    popupScore.classList.add('popup--show');
    makeDisabledBobyInGame(); // —Å–∫—Ä—ã—Ç–∏–µ –±–æ–¥–∏
    makeDisabledBtnsInGame(); // —Å–∫—Ä—ã—Ç–∏–µ –∫–Ω–æ–ø–æ–∫
  }

  // –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –ø—Ä–æ–¥–æ–ª–∂–µ–Ω–∏—è –∏–≥—Ä—ã, –∑–∞–∫—Ä—ã—Ç–∏—è –ø–æ–ø–∞–ø–∞, –æ–±–Ω—É–ª–µ–Ω–∏—è –∂–∏–∑–Ω–µ–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤—Ä–∞–≥–æ–≤
  let btnContinueGameClickHandler = function () {
    window.mechanicsGame.resetResultOfEnemy(); // –æ–±–Ω—É–ª–∏—Ç—å –∂–∏–∑–Ω—å —Ç–æ–ª—å–∫–æ –≤—Ä–∞–≥–∞–º
    window.enemyCreate.resetEnemyComponents(); // —Å–∫–∏–Ω—É—Ç—å –æ—Ç—Ä–µ–Ω–¥–æ—Ä–µ–Ω–Ω—ã–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –≤—Ä–∞–≥–∞
    window.mechanicsGame.changelifeLineCharacter(-50); // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –≥–µ—Ä–æ—é 50–•–ü
    popupScore.classList.remove('popup--show'); // –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–∞
    activateBtnsInGame();
    activatedBobyInGame();
  }

  // –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –≤—ã—Ö–æ–¥–∞ –∏–∑ –∏–≥—Ä—ã, –ø–æ–ø–∞–ø–∞ –∏ –æ–±–Ω—É–ª–µ–Ω–∏—è –∂–∏–∑–Ω–µ–π, –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤—Ä–∞–≥–æ–≤
  let btnExitFromGameAndPopapClickHandler = function () {
    window.mechanicsGame.resetResultsOfLife(); // –æ–±–Ω—É–ª–∏—Ç—å –∂–∏–∑–Ω—å –≤—Å–µ–º –ø–µ—Ä—Å–∞–º
    window.enemyCreate.resetEnemyComponents(); // —Å–∫–∏–Ω—É—Ç—å –æ—Ç—Ä–µ–Ω–¥–æ—Ä–µ–Ω–Ω—ã–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –≤—Ä–∞–≥–∞
    popupScore.classList.remove('popup--show'); // –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–∞
    activateBtnsInGame();
    activatedBobyInGame();
    hidePlayingField(); // –≤—ã—Ö–æ–¥ –∏–∑ –∏–≥—Ä–æ–≤–æ–≥–æ –ø–æ–ª—è
  }

  btnPopupContinueGame.addEventListener('click', btnContinueGameClickHandler);
  btnPopupExitFromGame.addEventListener('click', btnExitFromGameAndPopapClickHandler);

  /* ----- –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –∏–≥—Ä—ã ----- */

  // —Ñ—É–Ω–∫—Ü–∏—è —Å–∫—Ä—ã—Ç–∏—è –ø–æ–ª—è —Ñ–æ—Ä–º—ã
  function hideFormField() {
    pageHeaderGame.classList.add('page-header-game--hidden');
    slogan.classList.add('slogan--hidden');
    form.classList.add('form--hidden');
    pageFooter.classList.add('page-footer--hidden');
    game.classList.add('game--show');
  }

  function init() {
    hideFormField();
    window.enemyCreate.renderEnemy(); // —Ä–µ–Ω–¥–æ—Ä–∏–Ω–≥ –≤—Ä–∞–≥–∞
    makeDisabledBtnsInGame(); // –¥–µ–∞–∫—Ç–∏–≤–∏—Ä—É–µ—Ç –∫–Ω–æ–ø–∫–∏ –≤ –∏–≥—Ä–µ
    window.animation.animateCharacter();
    window.animation.animateEnemy();
    showLevelMessage(); // –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –∏–Ω—Ñ—É –æ —Ä–∞—É–Ω–¥–µ
    showPopupIncantations(); // –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –∑–∞–∫–ª–∏–Ω–∞–Ω–∏–π
    window.modalContainer.addEventLevelCheckOne();
    window.modalContainer.addEventLevelCheckResult();
  }

  // –ø–µ—Ä–µ–Ω–æ—Å –≤ –≥–ª–æ–±–∞–ª—å–Ω—É—é –æ–±–ª–∞—Å—Ç—å –≤–∏–¥–∏–º–æ—Å—Ç–∏
  window.popups = {
    incantationBtn: incantationBtn,
    game: game,
    btnsInGame: btnsInGame,
    activateBtnsInGame: activateBtnsInGame,
    activatedBobyInGame: activatedBobyInGame,
    showPopupIncantations: showPopupIncantations,
    showLevelMessage: showLevelMessage,
    showPopupScore: showPopupScore
  };
})();
