'use strict';

(function () {
  const enemy = document.querySelector('.game__enemy');
  const bodys = document.querySelector('.game__body-enemy');
  const shorts = document.querySelector('.game__shorts-enemy');
  const suits = document.querySelector('.game__suits-enemy');
  const hands = document.querySelector('.game__hands-enemy');
  const face = document.querySelector('.game__face-enemy');
  const eyes = document.querySelector('.game__eyes-enemy');
  const mouth = document.querySelector('.game__mouth-enemy');
  const hats = document.querySelector('.game__hat-enemy');
  const enemyName = document.querySelector('.game__nickname--enemy');

  function getRandomElementFromArray(array){
    let rand = Math.floor(Math.random() * array.length);
    return (array[rand]);
  }

  function renderEnemy() {
    bodys.appendChild(window.base–°onfig.enemyesArrayBody[0]);
    shorts.appendChild(getRandomElementFromArray(window.base–°onfig.enemyesArrayShorts));
    suits.appendChild(getRandomElementFromArray(window.base–°onfig.enemyesArraySuits));
    face.appendChild(window.base–°onfig.enemyesArrayFace[1]);
    hands.appendChild(window.base–°onfig.enemyesArrayHands[2]);
    eyes.appendChild(getRandomElementFromArray(window.base–°onfig.enemyesArrayEyes));
    mouth.appendChild(getRandomElementFromArray(window.base–°onfig.enemyesArrayMouth));
    hats.appendChild(getRandomElementFromArray(window.base–°onfig.enemyesArrayHats));

    enemyName.appendChild(document.createTextNode(
      getRandomElementFromArray(window.base–°onfig.enemyFirstPartName) + ' '
       + getRandomElementFromArray(window.base–°onfig.enemySeconPartdName) + ' '
       + getRandomElementFromArray(window.base–°onfig.enemyThirdPartName)));
  }

  // –æ–±–Ω—É–ª–µ–Ω–∏–µ —Ç–µ–ª–∞ –∏ –∏–º–µ–Ω–∏ –≤—Ä–∞–≥–∞
  function resetEnemyComponents() {
    for (let i = 0; i < enemy.children.length; i++) {
      enemy.children[i].innerHTML = '';
    }
    enemyName.innerHTML = '';
  }

  window.enemyCreate = {
    getRandomElementFromArray:getRandomElementFromArray,
    renderEnemy: renderEnemy,
    resetEnemyComponents: resetEnemyComponents
  };

})();
