'use strict';

(function () {
  const character = document.querySelector('.game__character');
  const enemy = document.querySelector('.game__enemy');

  function animate(draw, duration) { // –†–∏—Å—É–µ—Ç —Ñ—É–Ω–∫—Ü–∏—è draw, –ø—Ä–æ–¥–æ–ª–∂–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å –∞–Ω–∏–º–∞—Ü–∏–∏ duration
    let start = performance.now();
    requestAnimationFrame(function animate(time) {
      let timePassed = time - start; // –æ–ø—Ä–µ–¥–µ–ª–∏—Ç—å, —Å–∫–æ–ª—å–∫–æ –ø—Ä–æ—à–ª–æ –≤—Ä–µ–º–µ–Ω–∏ —Å –Ω–∞—á–∞–ª–∞ –∞–Ω–∏–º–∞—Ü–∏–∏
      if (timePassed > duration) timePassed = duration; // –≤–æ–∑–º–æ–∂–Ω–æ –Ω–µ–±–æ–ª—å—à–æ–µ –ø—Ä–µ–≤—ã—à–µ–Ω–∏–µ –≤—Ä–µ–º–µ–Ω–∏, –≤ —ç—Ç–æ–º —Å–ª—É—á–∞–µ –∑–∞—Ñ–∏–∫—Å–∏—Ä–æ–≤–∞—Ç—å –∫–æ–Ω–µ—Ü
      draw(timePassed); // –Ω–∞—Ä–∏—Å–æ–≤–∞—Ç—å —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∞–Ω–∏–º–∞—Ü–∏–∏ –≤ –º–æ–º–µ–Ω—Ç timePassed
      if (timePassed <= duration) { // –µ—Å–ª–∏ –≤—Ä–µ–º—è –∞–Ω–∏–º–∞—Ü–∏–∏ –Ω–µ –∑–∞–∫–æ–Ω—á–∏–ª–æ—Å—å - –∑–∞–ø–ª–∞–Ω–∏—Ä–æ–≤–∞—Ç—å –µ—â—ë –∫–∞–¥—Ä
        requestAnimationFrame(animate);
      }
    });
  }

  function animateEnemy() {
    animate(function(timePassed) {
      enemy.style.right = timePassed / 5 + 'px';
    }, 2300);
  };

  function animateCharacter() {
    animate(function(timePassed) {
      character.style.left = timePassed / 5 + 'px';
    }, 2300);
  };  

  // –ø–µ—Ä–µ–Ω–æ—Å –≤ –≥–ª–æ–±–∞–ª—å–Ω—É—é –æ–±–ª–∞—Å—Ç—å –≤–∏–¥–∏–º–æ—Å—Ç–∏
  window.animation = {
    animateCharacter: animateCharacter,
    animateEnemy: animateEnemy,
    character: character
  };
})();
