'use strict';

(function () {
  let currentTaskIndex = 0;
  const gameLevelMessage = document.querySelector('.game__level-message');
  const popupIncantations = document.querySelector('.popup--incantations');
  const levelNumber = document.querySelector('.game__message-span');
  const gameLevelNumber = document.querySelector('.game__level-span');


  function initNextLevel(){
    console.log(window.logicGame.currentTaskIndex);
    levelNumber.textContent = window.logicGame.currentTaskIndex+1;
    gameLevelNumber.textContent = window.logicGame.currentTaskIndex+1;
    window.modalContainer.changeContext();
    window.popups.showLevelMessage();

    window.modalContainer.addEventLevelCheckOne();
    window.modalContainer.addEventLevelCheckResult();

    window.popups.showPopupIncantations();
    popupIncantations.classList.remove('popup--hidden');



  }

  function getNextLevelTask(){
    currentTaskIndex++;
    if (currentTaskIndex == 4) currentTaskIndex = 0;
    return currentTaskIndex;
  }


  // –ø–µ—Ä–µ–Ω–æ—Å –≤ –≥–ª–æ–±–∞–ª—å–Ω—É—é –æ–±–ª–∞—Å—Ç—å –≤–∏–¥–∏–º–æ—Å—Ç–∏
  window.logicGame = {
    currentTaskIndex: currentTaskIndex,
    getNextLevelTask: getNextLevelTask,
    initNextLevel: initNextLevel

  };
})();
