'use strict';

(function () {
  const lifeLineCharacter = document.querySelector('.game__life-line--character'); // –ª–∏–Ω–∏—è –∂–∏–∑–Ω–∏
  const lifeLineEnemy = document.querySelector('.game__life-line--enemy');
  const amountLineCharacter = document.querySelector('.game__life-amount--character'); // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∂–∏–∑–Ω–∏
  const amountLineEnemy = document.querySelector('.game__life-amount--enemy');

  const textResultRoundInPopupScore = document.querySelector('.popup__text--result-round');
  const scoreInPopupScore = document.querySelector('.popup__text--score');
  const textFinalInPopupScore = document.querySelector('.popup__text--final');

  window.counterLifeCharacter = 100; // –Ω–∞—á–∞–ª—å–Ω—ã–π —Å—á—ë—Ç—á–∏–∫ –∂–∏–∑–Ω–∏ –≥–µ—Ä–æ—è
  window.counterLifeEnemy = 100; // –Ω–∞—á–∞–ª—å–Ω—ã–π —Å—á—ë—Ç—á–∏–∫ –∂–∏–∑–Ω–∏ –≤—Ä–∞–≥–∞

  // —Ñ—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –•–ü –≥–µ—Ä–æ—è
  function changelifeLineCharacter(damage) {
    let lifeCharacter = parseInt(lifeLineCharacter.style.width);
    lifeCharacter = (window.counterLifeCharacter - damage);
    if (lifeCharacter > 100) {
      lifeCharacter = 100;
    }
    if (lifeCharacter < 0) {
      lifeCharacter = 0;
      //alert('–ø–æ–º–µ—Ä –≥–µ—Ä–æ–π');
      window.popups.showPopupScore(); // –≤—ã–∑–æ–≤ –ø–æ–ø–∞–ø–∞ —Å–æ —Å—á—ë—Ç–æ–º
      // TODO: –≤—ã–∑–æ–≤ —Ñ–∏–Ω–∞–ª—å–Ω–æ–π –º–æ–¥–∞–ª–∫–∏ —Å —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–º –∏ —Ç–∞–±–ª–∏—Ü–µ–π. –æ–±–Ω—É–ª–µ–Ω–∏–µ –∏–Ω–¥–µ–∫—Å–∞ —Ç—É—Ä–æ–≤
      textResultRoundInPopupScore.textContent = '–ü–µ—Ä—Å–æ–Ω–∞–∂–∏ –∏–∑ –Æ–∂–Ω–æ–≥–æ –ë—É—Ç–æ–≤–∞ –æ–¥–µ—Ä–∂–∞–ª–∏ –ø–æ–±–µ–¥—É.';
      scoreInPopupScore.textContent = 100 - window.counterLifeEnemy; // TODO: –ø–æ–º–µ–Ω—è—Ç—å –Ω–∞ –ø–æ–¥—Å—á—ë—Ç
    }
    lifeLineCharacter.style.width = lifeCharacter + '%';
    window.counterLifeCharacter = lifeCharacter;
    amountLineCharacter.innerHTML = window.counterLifeCharacter;
  }

  // —Ñ—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –•–ü –≤—Ä–∞–≥–∞
  function changelifeLineEnemy(damage) {
    let lifeEnemy = parseInt(lifeLineEnemy.style.width);
    lifeEnemy = (window.counterLifeEnemy - damage);
    if (lifeEnemy > 100) {
      lifeEnemy = 100;
    }
    if (lifeEnemy < 0) {
      lifeEnemy = 0;
      //alert('–ø–æ–º–µ—Ä –≤—Ä–∞–≥');
      window.popups.showPopupScore(); // –≤—ã–∑–æ–≤ –ø–æ–ø–∞–ø–∞ —Å–æ —Å—á—ë—Ç–æ–º
      // TODO: –æ–±–Ω—É–ª–µ–Ω–∏–µ –∏–Ω–¥–µ–∫—Å–∞ —Ç—É—Ä–æ–≤, –æ—Ç—Ä–∏—Å–æ–≤–∫–∞ –Ω–æ–≤–æ–≥–æ –≥–µ—Ä–æ—è + 50 —Ö–ø –∏ 100 —Ö–ø –≤—Ä–∞–≥—É
      textResultRoundInPopupScore.textContent = '–ü–æ–∑–¥—Ä–∞–≤–ª—è–µ–º! –ó–∞ –ø–æ–±–µ–¥—É –Ω–∞–¥ –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫–æ–º –í—ã –ø–æ–ª—É—á–∞–µ—Ç–µ + 100 –±–∞–ª–ª–æ–≤.';
      textFinalInPopupScore.textContent = '–í—ã –ø—Ä–æ—à–ª–∏ –≤—Å—é –∏–≥—Ä—É, –±–∏—Ç—å –±–æ–ª—å—à–µ –Ω–µ–∫–æ–≥–æ..';
      scoreInPopupScore.textContent = '100'; // TODO: –ø–æ–º–µ–Ω—è—Ç—å –Ω–∞ –ø–æ–¥—Å—á—ë—Ç –±–∞–ª–ª–æ–≤
    }
    lifeLineEnemy.style.width = lifeEnemy + '%';
    window.counterLifeEnemy = lifeEnemy;
    amountLineEnemy.innerHTML = window.counterLifeEnemy;
  }

  // —Å–±—Ä–æ—Å —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤ —É—Ä–æ–≤–Ω—è –∂–∏–∑–Ω–∏ –≤—Ä–∞–≥–∞
  function resetResultOfEnemy() {
    lifeLineEnemy.style.width = 100 + '%';
    window.counterLifeEnemy = 100;
    amountLineEnemy.innerHTML = 100;
  }

  // —Å–±—Ä–æ—Å —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤ —É—Ä–æ–≤–Ω–µ–π –∂–∏–∑–Ω–∏ –ø–µ—Ä—Å–æ–Ω–∞–∂–µ–π
  function resetResultsOfLife() {
    lifeLineCharacter.style.width = 100 + '%';
    window.counterLifeCharacter = 100;
    amountLineCharacter.innerHTML = 100;
    resetResultOfEnemy();
  }

  function getRandomNumber(min, max) {
    return Math.round(Math.random() * (max - min) + min);
  }

  // —Ñ—É–Ω–∫—Ü–∏—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —É—Ä–æ–Ω–∞
  function selectOfDamage() {
    let tempIncantationIndicator = window.popups.incantationIndicator; // –≤—ã–±—Ä–∞–Ω–Ω–æ–µ –∑–∞–∫–ª–∏–Ω–∞–Ω–∏–µ
    if (tempIncantationIndicator == 'restore') {
      window.mechanicsGame.damageForEnemy = 1; // —É—Ä–æ–Ω –¥–ª—è –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫–∞
      window.mechanicsGame.increaseHealthForCharacter = getRandomNumber(-36, -30); // –ø–æ–ø–æ–ª–µ–Ω–Ω–∏–µ –•–ü –≤ —Å–ª—É—á–∞–µ –ø—Ä–∞–≤–∏–ª—å–Ω–æ–≥–æ –æ—Ç–≤–µ—Ç–∞
      window.mechanicsGame.damageFromEnemy = getRandomNumber(26, 35); // —É—Ä–æ–Ω –æ—Ç –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫–∞
    }
    if (tempIncantationIndicator == 'power1x') {
      window.mechanicsGame.damageForEnemy = getRandomNumber(21, 26);
      window.mechanicsGame.increaseHealthForCharacter = 0;
      window.mechanicsGame.damageFromEnemy = getRandomNumber(15, 21);
    }
    if (tempIncantationIndicator == 'power2x') {
      window.mechanicsGame.damageForEnemy = getRandomNumber(30, 35);
      window.mechanicsGame.increaseHealthForCharacter = 0;
      window.mechanicsGame.damageFromEnemy = getRandomNumber(26, 35);
    }
    console.log('—Å—á—ë—Ç—á–∏–∫ –∂–∏–∑–Ω–∏ –≥–µ—Ä–æ—è –¥–æ: ' + window.counterLifeCharacter);
    console.log('–ø–æ–ø–æ–ª–Ω–µ–Ω–∏–µ —Ö–ø: ' + window.mechanicsGame.increaseHealthForCharacter);
    console.log('–£—Ä–æ–Ω –≤—Ä–∞–≥—É: ' + window.mechanicsGame.damageForEnemy);
    console.log('–ó–∞–∫–ª–∏–Ω–∞–Ω–Ω–∏–µ –≤—ã–±—Ä–∞–Ω–Ω–æ: ' + tempIncantationIndicator);
  }

  // —É–º–µ–Ω—å—à–µ–Ω–∏—è –∂–∏–∑–Ω–∏ –ø–µ—Ä—Å–æ–≤ –¥–ª—è —Ç–µ—Å—Ç–æ–≤ - –í–†–ï–ú–ï–ù–ù–û
  // –ó–ê–ö–û–ú–ï–ù–ß–ï–ù–û –î–õ–Ø –ù–ê–ì–õ–Ø–î–ù–û–°–¢–ò
  //changelifeLineCharacter(90); // –í–†–ï–ú–ï–ù–ù–û !!
  //changelifeLineEnemy(90);

  // –ø–µ—Ä–µ–Ω–æ—Å –≤ –≥–ª–æ–±–∞–ª—å–Ω—É—é –æ–±–ª–∞—Å—Ç—å –≤–∏–¥–∏–º–æ—Å—Ç–∏
  window.mechanicsGame = {
    changelifeLineCharacter: changelifeLineCharacter,
    changelifeLineEnemy: changelifeLineEnemy,
    lifeLineCharacter: lifeLineCharacter,
    lifeLineEnemy: lifeLineEnemy,
    amountLineCharacter: amountLineCharacter,
    amountLineEnemy: amountLineEnemy,
    selectOfDamage: selectOfDamage,
    resetResultsOfLife: resetResultsOfLife,
    resetResultOfEnemy: resetResultOfEnemy
  };
})();
